ht:.7em;background:rgba(90,86,86,.44);border-radius:50%;margin:0 .7em}body .view .cover .header .points div.active{background:#333!important}body .view .cover .content{position:absolute;left:28%;right:26%;top:61%;border-radius:0 0 .5em .5em;padding:3em}body .view .cover .content .title{text-align:center;font-size:2em;font-family:RomulC}body .view .cover .content .text{margin-top:2em;font-size:1.1em;line-height:1.4em;text-align:justify}@font-face{font-family:RomulC;src:url(/fonts/RomulC.ttf)}